/**
 * WordPress dependencies
 */
import domReady from '@wordpress/dom-ready';

/**
 * External dependencies
 */
import type { HeatmapFrameAction } from '@nab/types';

/**
 * Internal dependencies
 */
import { reactToParentMessages as resolutionReact } from './screen-resolution';
import { reactToParentMessages as heatmapReact } from './heatmap';
import { reactToParentMessages as scrollmapReact } from './scrollmap';
import { reactToParentMessages as confettiReact } from './confetti';
import { loadResults } from './store';

import type { Dispatch, Select } from './types';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';

// Data Status Diagram:
//   » waiting-script » script-loaded » loading » processing » ready
//     »» error (rationale)

function init() {
	if ( ! hasWordPressData( window.parent ) ) {
		console.error( 'Parent window doesn’t have expected API. Bye!' ); // eslint-disable-line
		return;
	} //end if

	const { dispatch } = window.parent.wp.data;
	const { setPageAttribute } = dispatch( NAB_DATA );
	setPageAttribute( 'heatmap/dataStatus', { mode: 'script-loaded' } );

	const { select } = window.parent.wp.data;
	const { getExperiment, getPageAttribute } = select( NAB_DATA );

	const experimentId = getPageAttribute( 'editor/activeExperiment' ) ?? 0;
	const alternativeId = getPageAttribute( 'heatmap/activeAlternative' ) ?? '';
	const experiment = getExperiment( experimentId );
	if ( ! experiment ) {
		setPageAttribute( 'heatmap/dataStatus', {
			mode: 'error',
			rationale: 'experiment-not-found',
		} );
		return;
	} //end if

	const alternativeIndex = ( experiment.alternatives ?? [] )
		.map( ( { id } ) => id )
		.indexOf( alternativeId );
	if ( -1 === alternativeIndex ) {
		setPageAttribute( 'heatmap/dataStatus', {
			mode: 'error',
			rationale: 'alternative-not-found',
		} );
		return;
	} //end if

	const options = {
		awsAuthToken: getPageAttribute( 'heatmap/awsAuthToken' ) ?? '',
		siteId: getPageAttribute( 'heatmap/siteId' ) ?? '',
		timezone: getPageAttribute( 'heatmap/timezone' ) ?? '',
		dispatch: window.parent.wp.data.dispatch,
	};

	fixBodyHeight();
	window.addEventListener( 'message', reactToParentMessages, false );
	loadResults( experiment, alternativeIndex, options );
} //end init()

function reactToParentMessages( message: { data: HeatmapFrameAction } ) {
	if ( ! hasWordPressData( window.parent ) ) {
		return;
	} //end if

	const action = message.data;
	const { dispatch } = window.parent.wp.data;

	resolutionReact( action, dispatch );
	heatmapReact( action );
	scrollmapReact( action );
	confettiReact( action, dispatch );
} //end reactToParentMessages()

function fixBodyHeight() {
	try {
		document.body.style.height = 'auto';
	} catch ( e ) {}
} //end fixBodyHeight()

type WordPressData = {
	readonly wp: {
		readonly data: {
			readonly dispatch: Dispatch;
			readonly select: Select;
		};
	};
};

function hasWordPressData( win: unknown ): win is WordPressData {
	// eslint-disable-next-line @typescript-eslint/no-explicit-any, @typescript-eslint/no-unsafe-member-access
	return !! ( win as any ).wp.data;
} //end hasWordPressData()

// =======
// KICKOFF
// =======

domReady( init );
