/**
 * External dependencies.
 */
import type { HeatmapFrameAction, HeatmapResolution } from '@nab/types';

/**
 * Internal dependencies.
 */
import {
	areClickCoordinatesAlreadyComputedInResolution,
	computeClickCoordinatesInResolution as doComputeClickCoordinatesInResolution,
	recomputeClickCoordinatesInResolution as doRecomputeClickCoordinatesInResolution,
} from './store';
import { getDocumentHeight } from './utils';

import type { Dispatch } from './types';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';

const ELEMENT_HEIGHT_COMPUTATION_DELAY = 100;

type ElementHeights = {
	original: false | ReadonlyArray< OriHeights >;
} & Partial< Record< HeatmapResolution, ReadonlyArray< ResHeights > > >;

type OriHeights = {
	readonly el: HTMLElement;
	readonly cssHeight: string;
	readonly cssMinHeight: string;
};

type ResHeights = {
	readonly el: HTMLElement;
	actualHeight: string;
	didHeightChange?: boolean;
	readonly style: {
		height: string;
		maxHeight: string;
	};
};

const elementHeights: ElementHeights = {
	original: false,
};

const areProperHeightsComputed: Record< HeatmapResolution, boolean > = {
	desktop: false,
	smartphone: false,
	tablet: false,
};

export function reactToParentMessages(
	action: HeatmapFrameAction,
	dispatch: Dispatch
): void {
	switch ( action.type ) {
		case 'reset-heights':
			resetHeights( action.activeResolution, dispatch );
			break;

		case 'fetch-lazy-load-images':
			fetchLazyLoadImages( dispatch );
			break;

		case 'calculate-heights-in-double-sized-iframe':
			storeHeightsInDoubleSizedIFrame(
				action.activeResolution,
				dispatch
			);
			break;

		case 'calculate-actual-heights':
			storeProperHeightsAndMaximizeIFrame(
				action.activeResolution,
				dispatch
			);
			break;

		case 'fix-heights':
			fixInvalidHeights( action.activeResolution, dispatch );
			break;

		case 'compute-click-coordinates':
			computeClickCoordinatesInResolution(
				action.activeResolution,
				dispatch
			);
			break;

		case 'recompute-click-coordinates':
			recomputeClickCoordinatesInResolution(
				action.activeResolution,
				dispatch
			);
			break;
	} //end switch
} //end reactToParentMessages()

function resetHeights(
	activeResolution: HeatmapResolution,
	dispatch: Dispatch
) {
	if (
		areImagesAlreadyFetched &&
		areProperHeightsComputed[ activeResolution ]
	) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/iframeContentHeight',
			getDocumentHeight( activeResolution )
		);
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'fix-heights'
		);
		return;
	} //end if

	if ( elementHeights[ activeResolution ] ) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'fetch-lazy-load-images'
		);
		return;
	} //end if

	if ( ! elementHeights.original ) {
		const nabElements = Array.from(
			document.querySelectorAll< HTMLElement >(
				'.nab-overlay-element, .nab-overlay-element *'
			)
		);
		const elements = Array.from(
			document.querySelectorAll< HTMLElement >( '*' )
		).filter( ( el ) => ! nabElements.includes( el ) );

		elementHeights.original = elements.map( ( el ) => ( {
			el,
			cssHeight: el.style.height,
			cssMinHeight: el.style.minHeight,
		} ) );
	} //end if

	elementHeights.original.forEach( ( { el, cssHeight, cssMinHeight } ) => {
		el.style.height = cssHeight;
		el.style.minHeight = cssMinHeight;
	} );

	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/resolutionChangeMode',
		'fetch-lazy-load-images'
	);
} //end resetHeights()

let areImagesAlreadyFetched = false;
function fetchLazyLoadImages( dispatch: Dispatch ) {
	if ( areImagesAlreadyFetched ) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'calculate-heights-in-double-sized-iframe'
		);
		return;
	} //end if

	const images = Array.from(
		document.querySelectorAll< HTMLElement >( 'img[data-src]' )
	).reverse();
	images.forEach( ( img ) => {
		const src = img.getAttribute( 'data-src' );
		if ( src ) {
			img.setAttribute( 'src', src );
		} //end if
	} );

	const delay = images.length ? 500 : 0;
	setTimeout( () => {
		areImagesAlreadyFetched = true;
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'calculate-heights-in-double-sized-iframe'
		);
	}, delay );
} //end fetchLazyLoadImages()

function storeHeightsInDoubleSizedIFrame(
	activeResolution: HeatmapResolution,
	dispatch: Dispatch
) {
	if ( elementHeights[ activeResolution ] ) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'calculate-actual-heights'
		);
		return;
	} //end if

	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/iframeContentHeight',
		window.innerHeight * 50
	);

	setTimeout( () => {
		const nabElements = Array.from(
			document.querySelectorAll< HTMLElement >(
				'.nab-overlay-element, .nab-overlay-element *'
			)
		);
		const elements = Array.from(
			document.querySelectorAll< HTMLElement >( '*' )
		).filter( ( el ) => ! nabElements.includes( el ) );

		elementHeights[ activeResolution ] = elements.map( ( el ) => ( {
			el,
			actualHeight: window.getComputedStyle( el ).height,
			style: {
				height: el.style.height,
				maxHeight: el.style.maxHeight,
			},
		} ) );

		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'calculate-actual-heights'
		);
	}, ELEMENT_HEIGHT_COMPUTATION_DELAY );
} //end storeHeightsInDoubleSizedIFrame()

function storeProperHeightsAndMaximizeIFrame(
	activeResolution: HeatmapResolution,
	dispatch: Dispatch
) {
	if ( areProperHeightsComputed[ activeResolution ] ) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/iframeContentHeight',
			getDocumentHeight( activeResolution )
		);
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'fix-heights'
		);
		return;
	} //end if

	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/iframeContentHeight',
		window.innerHeight / 50
	);

	setTimeout( () => {
		elementHeights[ activeResolution ]?.forEach( ( data ) => {
			const actualHeight = window.getComputedStyle( data.el ).height;
			data.didHeightChange = data.actualHeight !== actualHeight;
			data.actualHeight = actualHeight;
			data.style.height = data.el.style.height;
			data.style.maxHeight = data.el.style.maxHeight;
		} );

		areProperHeightsComputed[ activeResolution ] = true;
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/iframeContentHeight',
			getDocumentHeight( activeResolution )
		);
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'fix-heights'
		);
	}, ELEMENT_HEIGHT_COMPUTATION_DELAY );
} //end storeProperHeightsAndMaximizeIFrame()

function fixInvalidHeights(
	activeResolution: HeatmapResolution,
	dispatch: Dispatch
) {
	elementHeights[ activeResolution ]?.forEach( ( info ) => {
		const { el, actualHeight, didHeightChange } = info;

		// Check if alt heights where set by another resolution.
		if ( 'true' === el.dataset.nabCustomHeights ) {
			el.style.height = '';
			el.style.minHeight = '';
		} //end if

		if ( ! didHeightChange ) {
			return;
		} //end if

		el.dataset.nabCustomHeights = 'true';
		el.style.height = actualHeight;
		el.style.minHeight = actualHeight;
	} );

	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/iframeContentHeight',
		getDocumentHeight( activeResolution )
	);
	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/resolutionChangeMode',
		'compute-click-coordinates'
	);
} //end fixInvalidHeights()

function computeClickCoordinatesInResolution(
	activeResolution,
	dispatch: Dispatch
) {
	if ( areClickCoordinatesAlreadyComputedInResolution( activeResolution ) ) {
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'none'
		);
		return;
	} //end if

	setTimeout( () => {
		doComputeClickCoordinatesInResolution( activeResolution );
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/iframeContentHeight',
			getDocumentHeight( activeResolution )
		);
		dispatch( NAB_DATA ).setPageAttribute(
			'heatmap/resolutionChangeMode',
			'none'
		);
	}, ELEMENT_HEIGHT_COMPUTATION_DELAY );
} //end computeClickCoordinatesInResolution()

function recomputeClickCoordinatesInResolution(
	activeResolution,
	dispatch: Dispatch
) {
	doRecomputeClickCoordinatesInResolution( activeResolution );
	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/resolutionChangeMode',
		'none'
	);
} //end recomputeClickCoordinatesInResolution()
