/**
 * WordPress dependencies
 */
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import * as StackBlur from 'stackblur-canvas';
import type {
	Dict,
	Dims,
	HeatmapFrameAction,
	HeatmapResolution,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { getScrolls } from './store';
import {
	createOverlayElement,
	getDocumentHeight,
	getMinMaxWidthInResolution,
	maximizeElement,
} from './utils';
import type { Scroll } from './types';

type Scrollmap = {
	averageFold?: HTMLElement;
	canvas?: HTMLCanvasElement;
	activeResolution: HeatmapResolution;
	data: Partial< Record< HeatmapResolution, ScrollmapData > >;
	images: Record< string, HTMLCanvasElement >;
	scrolls?: ReadonlyArray< Scroll >;
	stats?: HTMLElement;
	statsTracker?: HTMLElement;
};

type ScrollmapData = {
	readonly maxHits: number;
	readonly rows: ReadonlyArray< number >;
	readonly averageFold: number | false;
};

const scrollmap: Scrollmap = {
	activeResolution: 'desktop',
	data: {},
	images: {},
};

const ROW_HEIGHT = 50;
const COLORS = [
	'#000000',
	'#00031d',
	'#00052d',
	'#00094f',
	'#000c6a',
	'#000f7f',
	'#0014ac',
	'#0017c6',
	'#001dff',
	'#003dff',
	'#0074ff',
	'#009cff',
	'#00bcff',
	'#00d3ff',
	'#00ebff',
	'#00ffeb',
	'#00ffac',
	'#00ff74',
	'#00ff4d',
	'#2aff00',
	'#62ff00',
	'#91ff00',
	'#b1ff00',
	'#d1ff00',
	'#f8ff00',
	'#ffde00',
	'#ffbe00',
	'#ff9700',
	'#ff7700',
	'#ff5f00',
	'#ff4700',
	'#ff0000',
];

export function reactToParentMessages( action: HeatmapFrameAction ): void {
	if ( 'update-opacity' === action.type ) {
		setOverlayOpacity( action.value );
		return;
	} //end if

	if ( 'render-results' !== action.type ) {
		return;
	} //end if

	if ( 'scrollmap' !== action.mode ) {
		removeScrollmapFromBody();
		return;
	} //end if

	if ( ! action.areResultsLoaded ) {
		return;
	} //end if

	if ( ! scrollmap.scrolls ) {
		scrollmap.scrolls = getScrolls();
	} //end if

	if ( ! scrollmap.canvas ) {
		initScrollmap( action.activeResolution );
	} //end if

	scrollmap.activeResolution = action.activeResolution;

	setOverlayOpacity( action.opacity );
	appendScrollmapInBody( action.activeResolution );
	renderScrollmap( action.activeResolution, action.resolutions );
} //end reactToParentMessages()

function setOverlayOpacity( opacity: number ) {
	if ( ! scrollmap.canvas || undefined === opacity ) {
		return;
	} //end if
	scrollmap.canvas.style.opacity = `${ opacity / 100 }`;
} //end setOverlayOpacity()

function removeScrollmapFromBody() {
	if ( document.body === scrollmap.canvas?.parentNode ) {
		document.body.removeChild( scrollmap.canvas );
	} //end if

	if ( document.body === scrollmap.stats?.parentNode ) {
		document.body.removeChild( scrollmap.stats );
	} //end if

	if ( document.body === scrollmap.averageFold?.parentNode ) {
		document.body.removeChild( scrollmap.averageFold );
	} //end if

	if ( document.body === scrollmap.statsTracker?.parentNode ) {
		document.body.removeChild( scrollmap.statsTracker );
	} //end if
} //end removeScrollmapFromBody()

function appendScrollmapInBody( resolution: HeatmapResolution ) {
	if (
		! scrollmap.averageFold ||
		! scrollmap.stats ||
		! scrollmap.canvas ||
		! scrollmap.statsTracker
	) {
		return;
	} //end if

	scrollmap.averageFold.style.width = `${ document.body.clientWidth }px`;
	scrollmap.stats.style.width = `${ document.body.clientWidth }px`;
	maximizeElement( scrollmap.canvas, resolution );
	maximizeElement( scrollmap.statsTracker, resolution );

	if ( document.body === scrollmap.canvas.parentNode ) {
		return;
	} //end if

	document.body.appendChild( scrollmap.canvas );
	document.body.appendChild( scrollmap.averageFold );
	document.body.appendChild( scrollmap.stats );
	document.body.appendChild( scrollmap.statsTracker );
} //end appendScrollmapInBody()

function initScrollmap( resolution ) {
	// Canvas.
	scrollmap.canvas = createOverlayElement< HTMLCanvasElement >(
		'canvas',
		resolution
	);
	scrollmap.canvas.style.opacity = '0.75';

	// Average Fold.
	scrollmap.averageFold = createOverlayElement( 'div', resolution );
	scrollmap.averageFold.style.display = 'none';
	scrollmap.averageFold.style.height = 'auto';
	scrollmap.averageFold.style.borderBottom = '2px dashed #262b2d';
	scrollmap.averageFold.style.textAlign = 'center';

	const averageFoldContainer = createElement( 'span', {
		style: {
			fontSize: '18px',
			margin: '0 auto',
			borderRadius: '4px',
			fontWeight: '600',
			fontFamily:
				'-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif',
			background: '#262b2d',
			pointerEvents: 'none',
			color: '#eee',
			display: 'inline-block',
			transform: 'translate(-50%, -50%)',
			position: 'absolute',
			whiteSpace: 'nowrap',
		},
	} );

	scrollmap.averageFold.appendChild( averageFoldContainer );
	const scrollmapSpan = createElement( 'span', {
		className: 'nab-data-block',
		style: { padding: '0 1em' },
	} );
	const scrollmapSpanIcon = createElement( 'span', {
		className: 'dashicons dashicons-image-flip-vertical',
		style: {
			verticalAlign: 'text-top',
			marginRight: '5px',
		},
	} );
	const scrollmapLabel = document.createTextNode(
		_x( 'Average Fold:', 'text', 'nelio-ab-testing' ) + ' '
	);
	const scrollmapValue = createElement( 'output', {
		id: 'nab-average-fold',
	} );
	averageFoldContainer.appendChild( scrollmapSpan );
	scrollmapSpan.appendChild( scrollmapSpanIcon );
	scrollmapSpan.appendChild( scrollmapLabel );
	scrollmapSpan.appendChild( scrollmapValue );

	// Stats.
	scrollmap.stats = createOverlayElement( 'div', resolution );
	scrollmap.stats.style.height = 'auto';
	scrollmap.stats.style.borderBottom = '2px dashed #262b2d';
	scrollmap.stats.style.opacity = '0';
	scrollmap.stats.style.transition = 'opacity 200ms ease-in-out';

	const scrollStatsContainer = createElement( 'span', {
		style: {
			fontSize: '18px',
			left: '1em',
			borderRadius: '4px',
			fontWeight: '600',
			fontFamily:
				'-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif',
			background: '#262b2d',
			pointerEvents: 'none',
			color: '#eee',
			display: 'inline-block',
			position: 'absolute',
			transform: 'translate(0, -50%)',
			whiteSpace: 'nowrap',
		},
	} );

	const statsPopularity = createElement( 'span', {
		className: 'nab-data-block',
		style: { padding: '0 1em' },
	} );
	const statsPopularityLabel = document.createTextNode(
		_x( 'Popularity:', 'text', 'nelio-ab-testing' ) + ' '
	);
	const statsPopularityValue = createElement( 'output', {
		id: 'nab-popularity',
	} );
	scrollStatsContainer.appendChild( statsPopularity );
	statsPopularity.appendChild( statsPopularityLabel );
	statsPopularity.appendChild( statsPopularityValue );

	// Stats tracker.
	scrollmap.statsTracker = createOverlayElement( 'div', resolution );

	// Event listeners.
	scrollmap.stats.appendChild( scrollStatsContainer );

	scrollmap.statsTracker.addEventListener( 'mouseenter', () => {
		if ( scrollmap.stats ) {
			scrollmap.stats.style.opacity = '1';
		} //end if
	} );

	scrollmap.statsTracker.addEventListener( 'mouseleave', () => {
		if ( scrollmap.stats ) {
			scrollmap.stats.style.opacity = '0';
		} //end if
	} );

	scrollmap.statsTracker.addEventListener( 'mousemove', ( event ) => {
		const data = scrollmap.data[ scrollmap.activeResolution ];
		if ( ! data ) {
			return;
		} //end if

		const { rows, maxHits } = data;
		const rowIndex = Math.floor( event.y / ROW_HEIGHT );
		const intensity = rows[ rowIndex ];
		const popularity = Math.round( ( 100 * intensity ) / maxHits ) || 0;

		const popularityEl = document.getElementById( 'nab-popularity' );
		if ( ! popularityEl || ! scrollmap.stats ) {
			return;
		} //end if

		popularityEl.textContent = `${ popularity.toLocaleString() }%`;
		scrollmap.stats.style.top = `${ Math.max( event.y, 0 ) }px`;
	} );
} //end initScrollmap()

function renderScrollmap(
	activeResolution: HeatmapResolution,
	resolutions: Record< HeatmapResolution, Dims >
): void {
	if ( ! scrollmap.images[ activeResolution ] ) {
		generateScrollmapImage( activeResolution, resolutions );
	} //end if

	if ( ! scrollmap.canvas ) {
		return;
	} //end if

	const image = scrollmap.images[ activeResolution ];
	const ctx = scrollmap.canvas.getContext( '2d' );
	if ( image && ctx ) {
		scrollmap.canvas.width = image.width;
		scrollmap.canvas.height = image.height;
		ctx.clearRect( 0, 0, scrollmap.canvas.width, scrollmap.canvas.height );
		ctx.drawImage( image, 0, 0 );
	} //end if

	renderAverageFold( activeResolution, resolutions );
} //end renderScrollmap()

function renderAverageFold(
	activeResolution: HeatmapResolution,
	resolutions: Record< HeatmapResolution, Dims >
) {
	const avgFoldEl = document.getElementById( 'nab-average-fold' );
	if ( ! avgFoldEl || ! scrollmap.averageFold ) {
		return;
	} //end if

	const firstFoldPosition = resolutions[ activeResolution ].height;
	const { averageFold } = scrollmap.data[ activeResolution ] || {};
	if ( averageFold ) {
		avgFoldEl.textContent = `${ averageFold.toLocaleString() }px`;
		scrollmap.averageFold.style.top = `${ firstFoldPosition }px`;
		scrollmap.averageFold.style.display = 'block';
	} else {
		avgFoldEl.textContent = `–`;
		scrollmap.averageFold.style.display = 'none';
	} //end if
} //end renderAverageFold()

function generateScrollmapImage(
	activeResolution: HeatmapResolution,
	resolutions: Record< HeatmapResolution, Dims >
) {
	if ( ! scrollmap.data[ activeResolution ] ) {
		processScrollDataForCurrentResolution( activeResolution, resolutions );
	} //end if

	const data = scrollmap.data[ activeResolution ];
	if ( ! data ) {
		return;
	} //end if

	const { rows, maxHits } = data;
	const narrowCanvas = createElement< HTMLCanvasElement >( 'canvas' );
	narrowCanvas.width = 1;
	narrowCanvas.height = rows.length;
	const narrowCtx = narrowCanvas.getContext( '2d' );

	const colorIndexes = rows.map( ( hits ) =>
		Math.floor( ( hits / maxHits ) * ( COLORS.length - 1 ) )
	);
	smoothColorIndexTransition( colorIndexes ).forEach(
		( colorIndex, rowNumber ) => {
			if ( ! narrowCtx || ! scrollmap.canvas ) {
				return;
			} //end if
			narrowCtx.fillStyle = COLORS[ colorIndex ];
			narrowCtx.fillRect( 0, rowNumber, scrollmap.canvas.width, 1 );
		}
	);

	StackBlur.canvasRGB(
		narrowCanvas,
		0,
		0,
		narrowCanvas.width,
		narrowCanvas.height,
		100 / ROW_HEIGHT
	);
	scrollmap.images[ activeResolution ] = narrowCanvas;
} //end generateScrollmapImage()

function processScrollDataForCurrentResolution(
	activeResolution: HeatmapResolution,
	resolutions: Record< HeatmapResolution, Dims >
) {
	const bodyHeight = window.document.body.clientHeight;
	const iframeHeight = getDocumentHeight( activeResolution );
	const numOfRows = Math.ceil( iframeHeight / ROW_HEIGHT );
	const rows = new Array( numOfRows ).fill( 0 );

	let heightFromTop = 0;

	const scrollsInResolution = getScrollsInResolution(
		scrollmap.scrolls ?? [],
		activeResolution,
		resolutions
	);

	scrollsInResolution.forEach(
		( { firstFold, maxScroll, sweetSpot, documentHeight } ) => {
			// Accumulate first fold heights.
			heightFromTop += firstFold;

			maxScroll = Math.round(
				( maxScroll / documentHeight ) * bodyHeight
			);
			sweetSpot = Math.round(
				( sweetSpot / documentHeight ) * bodyHeight
			);

			const maxScrollRow = Math.ceil( maxScroll / ROW_HEIGHT );

			// Add max scrolling relevance.
			const lastScrollRow = Math.min( maxScrollRow, numOfRows - 1 );
			for ( let i = 0; i <= lastScrollRow; ++i ) {
				rows[ i ] = ++rows[ i ] || 1;
			} //end for

			// Add sweetspot relevance.
			const sweetSpotMinRow = Math.floor(
				( sweetSpot - firstFold / 2 ) / ROW_HEIGHT
			);
			const sweetSpotMaxRow = Math.ceil(
				( sweetSpot + firstFold / 2 ) / ROW_HEIGHT
			);
			const lastSweetSpotRow = Math.min( sweetSpotMaxRow, numOfRows - 1 );
			for (
				let i = Math.max( 0, sweetSpotMinRow );
				i <= lastSweetSpotRow;
				++i
			) {
				rows[ i ] = ++rows[ i ] || 1;
			} //end for
		}
	);

	scrollmap.data[ activeResolution ] = {
		rows,
		maxHits: max( rows ),
		averageFold: !! scrollsInResolution.length
			? Math.ceil( heightFromTop / scrollsInResolution.length )
			: false,
	};
} //end processScrollData()

function getScrollsInResolution(
	scrolls: ReadonlyArray< Scroll >,
	activeResolution: HeatmapResolution,
	resolutions: Record< HeatmapResolution, Dims >
) {
	const { minWidth, maxWidth } = getMinMaxWidthInResolution(
		activeResolution,
		resolutions
	);
	return scrolls.filter(
		( { windowWidth } ) =>
			minWidth <= windowWidth && windowWidth <= maxWidth
	);
} //end getScrollsInResolution()

function smoothColorIndexTransition( values: ReadonlyArray< number > ) {
	let result = values;
	for ( let i = 0; i < 5; ++i ) {
		result = smooth( result );
	} //end for
	return result;
} //end smoothColorIndexTransition()

function smooth( values: ReadonlyArray< number > ) {
	if ( ! values.length ) {
		return values;
	} //end if

	const weighted = average( values );
	const originalMin = min( values );
	const originalMax = max( values );
	const originalSize = originalMax - originalMin;

	const numOfItems = values.length;
	const padding = 10;
	const firstValue = values[ 0 ] || 0;
	const lastValue = values[ values.length - 1 ] || 0;
	values = [
		...( new Array( padding ).fill( firstValue ) as number[] ),
		...values,
		...( new Array( padding ).fill( lastValue ) as number[] ),
	];

	let smoothed: number[] = [];
	for ( let i = 1; i < values.length - 1; ++i ) {
		const curr = values[ i ];
		const prev = smoothed[ i - 1 ] || values[ i - 1 ];
		const next = values[ i + 1 ];
		const improved = average( [ weighted, prev, curr, next ] );
		smoothed.push( improved );
	} //end for
	smoothed = smoothed.splice( padding - 1, numOfItems );

	const smoothedMin = min( smoothed );
	const smoothedMax = max( smoothed );
	const smoothedSize = smoothedMax - smoothedMin;
	const normalized = smoothed.map(
		( value ) => ( value - smoothedMin ) / smoothedSize
	);

	return normalized.map(
		( value ) => Math.round( value * originalSize ) + originalMin
	);
} //end smooth()

function average( values: ReadonlyArray< number > ) {
	const sum = values.reduce( ( res, val ) => res + val, 0 );
	const avg = sum / values.length;
	return avg;
} //end average()

function min( values: ReadonlyArray< number > ) {
	return values.reduce(
		( memo, value ) => Math.min( memo, value ),
		Number.POSITIVE_INFINITY
	);
} //end if

function max( values: ReadonlyArray< number > ) {
	return values.reduce(
		( memo, value ) => Math.max( memo, value ),
		Number.NEGATIVE_INFINITY
	);
} //end if

function createElement< T extends HTMLElement = HTMLElement >(
	type: string,
	options: Dict & { style?: Dict< string > } = {}
): T {
	const el = document.createElement( type );

	const { id, className, style, ...props } = options;
	if ( 'string' === typeof id ) {
		el.id = id;
	} //end if

	if ( 'string' === typeof className ) {
		el.className = className;
	} //end if

	if ( 'object' === typeof style ) {
		Object.keys( style ).forEach( ( k ) => {
			el.style[ k ] = style[ k ];
		} );
	} //end if

	Object.keys( props ).forEach( ( p ) => {
		el[ p ] = props[ p ];
	} );

	return el as T;
} //end createElement()
