/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';
import { _x } from '@wordpress/i18n';
import { addQueryArgs } from '@wordpress/url';

/**
 * External dependencies
 */
import type { Dims, Experiment, HeatmapResolution } from '@nab/types';

/**
 * Internal dependencies
 */
import { getElementByXPath } from '../../../public/tracking/regular/xpath';
import { getMinMaxWidthInResolution } from './utils';
import type {
	Click,
	HeatmapResults,
	ResultLoadingOptions,
	Scroll,
} from './types';

type BoundingBox = {
	readonly top: number;
	readonly left: number;
	readonly width: number;
	readonly height: number;
};

type AlternativeIndex = number;

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';

let isLoadingResults = false;
let results: false | HeatmapResults = false;
let areClickCoordinatesComputed: Record< HeatmapResolution, boolean > = {
	desktop: false,
	tablet: false,
	smartphone: false,
};
let boundingBoxes: Record<
	HeatmapResolution,
	Record< string, false | BoundingBox >
> = { desktop: {}, tablet: {}, smartphone: {} };

export function loadResults(
	experiment: Experiment,
	alternative: AlternativeIndex,
	options: ResultLoadingOptions
): void {
	if ( isLoadingResults ) {
		return;
	} //end if
	isLoadingResults = true;

	results = false;
	areClickCoordinatesComputed = {
		desktop: false,
		tablet: false,
		smartphone: false,
	};
	const { dispatch } = options;
	const { setPageAttribute } = dispatch( NAB_DATA );
	setPageAttribute( 'heatmap/dataStatus', { mode: 'loading' } );

	Promise.all( [
		paginatedLoad< Click >( 'clicks', experiment, alternative, options ),
		paginatedLoad< Scroll >( 'scrolls', experiment, alternative, options ),
	] )
		.then( ( [ clicks, scrolls ] ) => {
			setPageAttribute( 'heatmap/dataStatus', {
				mode: 'processing',
			} );
			isLoadingResults = false;
			results = { clicks, scrolls };
			beautifyClicks();
			setPageAttribute( 'heatmap/dataStatus', { mode: 'ready' } );
			setPageAttribute( 'heatmap/resolutionChangeMode', 'reset-heights' );
		} )
		.catch( () => {
			setPageAttribute( 'heatmap/dataStatus', {
				mode: 'error',
				rationale: 'promise-failed',
			} );
		} );
} //end loadResults()

export function getClicks(): HeatmapResults[ 'clicks' ] {
	if ( isLoadingResults ) {
		return [];
	} //end if

	if ( ! results ) {
		return [];
	} //end if

	if ( ! areClickCoordinatesComputed.desktop ) {
		return [];
	} //end if

	return results.clicks;
} //end getClicks()

export function getClicksInResolution(
	resolution: HeatmapResolution,
	availableResolutions: Record< HeatmapResolution, Dims >
): HeatmapResults[ 'clicks' ] {
	if ( isLoadingResults ) {
		return [];
	} //end if

	if ( ! results ) {
		return [];
	} //end if

	if ( ! areClickCoordinatesComputed[ resolution ] || ! results.clicks ) {
		return [];
	} //end if

	const { minWidth, maxWidth } = getMinMaxWidthInResolution(
		resolution,
		availableResolutions
	);
	return results.clicks.filter(
		( { realCoordinates: rc, windowWidthInPx: w } ) =>
			minWidth <= w && w <= maxWidth && !! rc?.[ resolution ]
	);
} //end getClicksInResolution()

export function getScrolls(): HeatmapResults[ 'scrolls' ] {
	if ( isLoadingResults ) {
		return [];
	} //end if

	if ( ! results ) {
		return [];
	} //end if

	return results.scrolls;
} //end getScrolls()

export function areClickCoordinatesAlreadyComputedInResolution(
	resolution: HeatmapResolution
): boolean {
	return !! areClickCoordinatesComputed[ resolution ];
} //end if

export function computeClickCoordinatesInResolution(
	resolution: HeatmapResolution
): void {
	if ( isLoadingResults || ! results ) {
		return;
	} //end if

	if ( areClickCoordinatesComputed[ resolution ] ) {
		return;
	} //end if

	computeClickCoordinates( resolution );
} //end computeClickCoordinatesInResolution()

export function recomputeClickCoordinatesInResolution(
	resolution: HeatmapResolution
): void {
	if ( isLoadingResults || ! results ) {
		return;
	} //end if

	areClickCoordinatesComputed = {
		desktop: false,
		tablet: false,
		smartphone: false,
	};
	boundingBoxes = { desktop: {}, tablet: {}, smartphone: {} };
	computeClickCoordinates( resolution );
} //end recomputeClickCoordinatesInResolution()

// =======
// HELPERS
// =======

function paginatedLoad< T >(
	what: 'clicks' | 'scrolls',
	experiment: Experiment,
	alternative: AlternativeIndex,
	options: ResultLoadingOptions
): Promise< ReadonlyArray< T > > {
	const { siteId } = options;

	const args = {
		alternative,
		start: experiment.startDate,
		end: experiment?.endDate,
		tz: options.timezone,
	};

	return new Promise( ( resolve ) => {
		const url = addQueryArgs(
			`https://api.nelioabtesting.com/v1/site/${ siteId }/experiment/${ experiment.id }/${ what }`,
			args
		);
		loadPage< T >(
			url,
			options.awsAuthToken,
			[] as ReadonlyArray< T >,
			resolve
		);
	} );
} //end paginatedLoad()

function loadPage< T >(
	url: string,
	authToken: string,
	result: ReadonlyArray< T >,
	resolve: ( r: unknown ) => void
): void {
	type Result = {
		readonly data: ReadonlyArray< T >;
		readonly page: number;
	};

	apiFetch< Result >( {
		url,
		method: 'GET',
		credentials: 'omit',
		mode: 'cors',
		headers: {
			Authorization: `Bearer ${ authToken }`,
		},
	} ).then(
		( { data, page } ) => {
			result = [ ...result, ...data ];
			const newUrl = addQueryArgs( url, { page } );
			if ( page && url !== newUrl ) {
				loadPage( newUrl, authToken, result, resolve );
			} else {
				resolve( result );
			} //end if
		},
		() => resolve( result )
	);
} //end loadPage()

function beautifyClicks(): void {
	if ( ! results ) {
		return;
	} //end if

	const UNKNOWN = _x( 'unknown', 'text', 'nelio-ab-testing' );
	results = {
		...results,
		clicks: results.clicks.map( ( click ) => ( {
			...click,
			browser: click.browser || UNKNOWN,
			country: click.country || UNKNOWN,
			dayOfWeek: getDayOfWeekCategory( click.dayOfWeek ),
			device: click.device || UNKNOWN,
			hourOfDay: getHourOfDayCategory( click.hourOfDay ),
			intensity: click.intensity || 1,
			os: click.os || UNKNOWN,
			timeToClick: getTimeToClickCategory( click.timeToClick ),
			windowWidth: getWindowWidthCategory( click.windowWidth ),
			windowWidthInPx:
				'number' === typeof click.windowWidth
					? click.windowWidth
					: Number.parseInt( click.windowWidth ) || 0,
			isVisibleInResolution: {},
			realCoordinates: {},
		} ) ),
	};
} //end beautifyClicks()

function computeClickCoordinates( resolution: HeatmapResolution ): void {
	if ( ! results ) {
		return;
	} //end if

	results.clicks.forEach( ( click ) => {
		const { x, y } = click;
		const { top, left, width, height } = getElementBoundingBox(
			click,
			resolution
		) || { top: 0, left: 0, width: 0, height: 0 };

		const realX = left + x * width;
		const realY = top + y * height;

		click.realCoordinates = click.realCoordinates || {};
		click.realCoordinates[ resolution ] = {
			...( click.realCoordinates[ resolution ] || {} ),
			x: realX,
			y: realY,
		};

		click.isVisibleInResolution = click.isVisibleInResolution || {};
		click.isVisibleInResolution[ resolution ] = width > 0 && height > 0;
	} );

	areClickCoordinatesComputed[ resolution ] = true;
} //end computeClickCoordinates()

function getElementBoundingBox(
	click: Click,
	resolution: HeatmapResolution
): BoundingBox | false {
	const selector = click.xpath || click.cssPath;
	if ( ! selector ) {
		return false;
	} //end if

	if ( undefined !== boundingBoxes[ resolution ][ selector ] ) {
		return boundingBoxes[ resolution ][ selector ];
	} //end if

	let el: Node | null = null;
	try {
		el = click.xpath
			? getElementByXPath( selector )
			: document.querySelector( selector );
	} catch ( e ) {
		console.log( 'OOPS', click.xpath );
	} //end if

	if ( isHTMLElement( el ) ) {
		boundingBoxes[ resolution ][ selector ] = el.getBoundingClientRect();
	} else {
		boundingBoxes[ resolution ][ selector ] = false;
	} //end if

	return boundingBoxes[ resolution ][ selector ];
} //end getElementBoundingBox()

function getHourOfDayCategory( hourOfDay: string | number ): string {
	if ( 'string' === typeof hourOfDay ) {
		hourOfDay = Number.parseInt( hourOfDay );
		if ( isNaN( hourOfDay ) ) {
			hourOfDay = 0;
		} //end if
	} //end if
	return `${ Math.floor( hourOfDay / 2 ) }`;
} //end getHourOfDayCategory()

function getDayOfWeekCategory( dayOfWeek: string | number ): string {
	return `${ dayOfWeek }`;
} //end getDayOfWeekCategory()

function getTimeToClickCategory( timeToClick: string | number ): string {
	if ( 'string' === typeof timeToClick ) {
		timeToClick = Number.parseInt( timeToClick );
	} //end if
	const cuts = [ 2, 3, 4, 5, 10, 15, 20, 25, 30, 60, 120, 180, 300 ];
	for ( let i = 0; i < cuts.length; ++i ) {
		if ( timeToClick <= cuts[ i ] ) {
			return `${ i }`;
		} //end if
	} //end for

	return `${ cuts[ cuts.length - 1 ] }`;
} //end getTimeToClickCategory()

function getWindowWidthCategory( windowWidth: string | number ): string {
	if ( 'string' === typeof windowWidth ) {
		windowWidth = Number.parseInt( windowWidth );
	} //end if

	const cuts = [ 300, 600, 900, 1200, 1500, 1800, 2100 ];
	for ( let i = 0; i < cuts.length; ++i ) {
		if ( windowWidth < cuts[ i ] ) {
			return `${ i }`;
		} //end if
	} //end for

	return `${ cuts[ cuts.length - 1 ] }`;
} //end getWindowWidthCategory()

function isHTMLElement( n: Node | null ): n is HTMLElement {
	return !! n && Node.ELEMENT_NODE === n.nodeType;
} //end isHTMLElement()
